.button {
    min-width: 200px;
    padding: 8px 15px;
    border: 2px solid #ccc;
    background: #000;
    color: #aaa;
    border-radius: 5px;
    font-size: 20px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    transition: all 400ms ease-out;
  }
  
  .button:hover {
    text-shadow: #fff -1px -1px 1px, #fff 1px 1px 1px;
    letter-spacing: 2px;
  }
  